<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
				<ctitle :title="'基本信息'"></ctitle>
        <div class="inner">
          <div class="photo">
            <img src="../img/photo.png" alt="">
          </div>
          <div class="name">{{jbxx.name}}</div>
          <div class="desc">
            <div class="desc-content">
              <div class="list">
                <span class="title">时间：</span>
                <span class="inner">{{jbxx.time}}</span>
              </div>
              <div class="list">
                <span class="title">信访行为分析指数：</span>
                <span class="colorful">{{jbxx.fxzs}}</span>
              </div>
              <div class="list">
                <span class="title">指数预警：</span>
                <p>{{jbxx.zsyj}} <span class="colorful">{{jbxx.zsmsg}}</span></p>
              </div>
              <div class="list">
                <span class="title">该信访人：</span>
                <div>
                  <p>扬言<span class="colorful">{{jbxx.yy}}</span>次，</p>
                  <p>过激行为<span class="colorful">{{jbxx.yy}}</span>次，</p>
                  <p>非访<span class="colorful">{{jbxx.yy}}</span>次</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="w-right">
        <div class="r-top">
          <ctitle :title="'信访行为分析指数'"></ctitle>
           <div class="inner">
             <LineMonth :params="zsLineParams"></LineMonth>
          </div>
          <div class="msg">*指数计算方式，由信访行为、扬言、过激行为、问题分类等因素通过不同权重配比计算产生</div>
        </div>
        <div class="r-bottom">
          <ctitle :title="'信访行为统计'"></ctitle>
          <div class="inner">
            <ul class="bottom-inner" >
              <!-- <li>2017年3月</li> -->
              <li v-for="(item, index) in xftjxw" 
                :key="index">{{item.day}} 
                <img src="../img/xing.png" alt="" v-if="index > 0 && index <xftjxw.length - 1">
                <div v-if="item && item.btn" class="box">
                  <div class="i-bottom" v-for="(nItem, nIndex) in item.btn" :key="nIndex">
                    <img src="../img/icon.png" alt="">
                    <span>{{nItem}}</span>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { get_http } from "@/api/index";
  import ctitle from "@/components/title/index";
  import LineMonth from "@/components/line/lineMonth";
  export default {
    name: 'xwgjyj',
    components: {
      ctitle,
      LineMonth
    },
    data() {
      return {
        xftjxw: [
          {
            day: '2017年3月'
          },
          {
            day: '2017年国庆',
            btn: ['扬言', "信访"]
          },
          {
            day: '2018年春节',
            btn: ['扬言']
          },
          {
            day: '2019年春节',
            btn: ['过激行为']
          },
          {
            day: '2019年劳动节',
            btn: ['非访']
          },
          {
            day: '2019年劳动节'
          },
        ],
        jbxx: {},
        // 信访行为分析指数
				zsLineParams: {
          data: [],
          colors: [
            {
              line: '#1a82f7',
              start: "rgba(181, 127, 182, 0)",
              end: "rgba(181, 127, 182, 0)"
            },
          ],
          option: {
            splitLineShow: false,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "月",
            axisYName: "",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "15%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "15%",
            yAxisIsNotShow: true,
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
				},
      }
    },
    mounted() {
      this.getAPI()
    },
    methods: {
      getAPI() {
        // 1.基本信息
        get_http('xfxwfxyjXwgjyjJbxx')
        .then(res => {
          this.jbxx = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });

        // 2.信访行为分析指数
        get_http("xfxwfxyjXwgjyjFxzs")
        .then(res => {
          this.zsLineParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      }
    }
  }
</script>

<style lang="stylus" scoped>
.wrapper
  position relative
  top 0.45rem
  height calc(100% - 45px)
  .content
    width 100%
    height 100%
    display flex
    padding .2rem .3rem
    box-sizing border-box
    .w-left
      flex 1
      .inner
        .photo
          margin 10px 0
          text-align center
        > .name
          font-size 0.3rem
          color #fff
          text-align center
          background url("../img/name-bg.png") no-repeat center center
        .desc
          width 3.9rem
          height 3.5rem
          margin 0 auto
          background url("../img/desc.png") no-repeat center center
          .desc-content
            padding: 0.3rem 0.5rem
            height 100%
            display flex
            flex-direction column
            justify-content space-around
            .list
              color #e2f6ff
              font-size 18px
              line-height 30px
              display flex
              .title
                color #5ecdff
                flex-shrink 0
              .inner
                color #e2f6ff
              .colorful
                color #f3498d
                font-weight bold
    .w-right
      flex 2
      display flex
      flex-direction column
      .r-top
        flex 1.5
        display flex
        flex-direction column
        .inner
          width 100%
          height 3rem
        .msg
          font-size 16px
          color rgb( 94, 205, 255 )
          text-align right
          padding-right 1.5rem
      .r-bottom
        flex 1

        .inner
          width 100%
          height 100%
          background url("../img/bg.png") no-repeat center 112px
          .bottom-inner
            display flex
            font-size 14px
            color #94e4f2
            position relative
            top 87px
            left 86px
            width 1100px
            justify-content space-around
            li
              display flex
              flex-direction column
              align-items center
              img 
                width 42px
                height 38px
                margin-top 12px
              .i-bottom
                display flex
                flex-direction column
                align-items center
                flex-shirnk 0
                // margin-left -124px
                // margin-top -8px
                position relative
                img 
                  width 23px
                  height 16px
                  margin-bottom 5px
              .box
                display flex
                position absolute
                bottom 12px
                right 95px
</style>